#output_container {
  overflow: hidden;
  white-space: nowrap;  
}

#log {
  color: #585858;
  font-size: 21px;
  height: 27px;
}

#result {
  line-height: 80px;
}

.intermediate_res {
  color: rgb(167, 167, 167);
}

.output_elem_wrap {
  border-radius: 2px;
  display: flex;
  justify-content: flex-end;
}

.oversized {
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.33) 0%, rgba(236,233,233, 0) 10%);
}

.log_margin_bottom {
  margin-bottom: 35px;
}